.items{
  &-slider{
    &__item{
      width: 250px;
    }
  }
}

.item{
  &-slide{
    border-radius: 12px;
    overflow: hidden;
    &__img{
      height: 150px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__title{
      font-weight: 600;
      font-size: 14px;
      color: #1C91FD;
    }
    &__info{
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
    }
    &__price{
      font-weight: 600;
      font-size: 12px;
    }
    &__text{
      font-weight: 500;
      font-size: 10px;
      color:#151515;
    }
    &__address{
      margin-top: 10px;
      font-size: 10px;
      color:#151515;
    }
    &__phone{
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: auto;

      position: relative;
      width: 100%;
      height: 35px;
      border:1px solid #DBE2E8;
      border-radius: 5px;

      font-size: 12px;
      font-weight: 500;
      svg{
        position: absolute;
        left: 18px;
        top: 50%;
        margin-top: -8px;

        width: 16px;
        height: 16px;
      }
    }
    &__content{
      height: 160px;
      padding: 14px 12px 18px 12px;

      display: flex;
      flex-direction: column;

      border-bottom-left-radius: 12px;
      border-bottom-right-radius: 12px;

      border: 1px solid #DBE2E8;
      border-top: none;
    }
  }
}

@media screen and (min-width: 991px) {
  .items{
    &-slider{
      &__item{
        width: 320px;
      }
    }
  }
  .item{
    &-slide{
      &__img{
        height: 200px;
      }
      &__title{
        font-size: 18px;
      }
      &__price{
        font-size: 16px;
      }
      &__text,&__address{
        font-size: 14px;
      }
      &__phone{
        height: 45px;
        font-size: 15px;
      }
      &__info,&__address{
        margin-top: 15px;
      }
      &__content{
        height: 215px;
        padding: 20px 15px 25px 15px;
      }
    }
  }
}