*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Medium/Graphik-Medium.woff2") format("woff2"), url("../fonts/Graphik-Medium/Graphik-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Regular/Graphik-Regular.woff2") format("woff2"), url("../fonts/Graphik-Regular/Graphik-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Bold/Graphik-Bold.woff2") format("woff2"), url("../fonts/Graphik-Bold/Graphik-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Graphik', sans-serif;
  font-weight: 400;
  color: #000;
}

.container {
  max-width: 1320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 600px) {
  .container {
    padding: 0 60px;
  }
}

.catalog-block__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.catalog-block__label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 32px;
  color: #151515;
}

.catalog-block__label svg {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.catalog-block__link {
  font-size: 18px;
  font-weight: 500;
  color: #FF4D35;
}

.catalog-block__container {
  margin-top: 25px;
}

.btn {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  cursor: pointer;
}

.btn-orange {
  background-color: #FF4D35;
  border-radius: 4px;
  font-size: 13px;
  color: #Fff;
  font-weight: 500;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.burger__item {
  width: 100%;
}

.catalog-burger {
  width: 14px;
  height: 12px;
  margin-right: 26px;
}

.catalog-burger .burger__item {
  height: 2px;
  background-color: #fff;
  border-radius: 30px;
}

@media screen and (max-width: 991px) {
  .catalog-block__label {
    font-size: 24px;
  }
  .catalog-block__link {
    font-size: 14px;
  }
  .catalog-block__container {
    padding-right: 0;
  }
}

.item-img-slider .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  opacity: .4;
}

.item-img-slider .swiper-pagination-bullet-active {
  opacity: 1;
}

.radio-input__item {
  display: none;
}

.text-input {
  -webkit-appearance: none;
  width: 100%;
  height: 55px;
  border: 1px solid #D2D8DE;
  border-radius: 7px;
  padding: 0 18px;
  font-size: 18px;
}

@media screen and (max-width: 991px) {
  .text-input {
    font-size: 16px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.header-row {
  border-bottom: 1px solid rgba(235, 242, 248, 0.1);
  display: flex;
  align-items: center;
}

.header-row_first {
  height: 72px;
}

.header-row_second {
  height: 55px;
}

.header-row__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-burger {
  width: 21px;
  height: 18px;
}

.header-burger .burger__item {
  background-color: #FFFFFF;
  height: 2px;
  border-radius: 30px;
}

.header__block_info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__block_burger {
  margin-left: 24px;
}

.header__logo .logo__img {
  width: 200px;
  height: 60px;
}

.header__phone, .header__address {
  color: #fff;
  font-size: 14px;
}

.header__phone {
  margin-bottom: 2px;
  font-weight: 500;
}

.header__btn {
  width: 140px;
  height: 35px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__text {
  text-transform: uppercase;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.5;
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.logo__img + .logo__text {
  margin-left: 50px;
}

.nav-list {
  display: flex;
  align-items: center;
}

.nav-list__item + .nav-list__item {
  margin-left: 40px;
}

.nav-list__item + .nav-list__item_catalog {
  margin-left: 65px;
}

.nav-item__link {
  font-weight: 500;
  font-size: 15px;
  color: #fff;
}

.nav-item__catalog {
  display: flex;
  align-items: center;
  padding-right: 32px;
  border-right: 1px solid rgba(221, 232, 241, 0.2);
}

@media screen and (min-width: 991px) {
  .header-row_second .header__block_info {
    display: none;
  }
  .header__block_burger {
    display: none;
  }
}

@media screen and (max-width: 990px) {
  .header__btn {
    margin-top: 33px;
  }
  .header__logo .logo__img {
    width: 140px;
    height: 40px;
  }
  .header-row_first .header__block_info {
    margin-left: auto;
  }
  .header-row_second {
    position: fixed;
    top: 0;
    left: 0;
    padding: 56px 0;
    background-color: #fff;
    width: 280px;
    height: 100vh;
    z-index: 99;
    align-items: flex-start;
    justify-content: flex-start;
    display: none;
  }
  .header-row_second .header-row__wrapper {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .header-row_second .header__block_info {
    margin-top: 40px;
    align-items: flex-start;
  }
  .header-row_second .header__address, .header-row_second .header__phone {
    color: #000;
  }
  .nav-list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .nav-list__item + .nav-list__item {
    margin-left: 0;
    margin-top: 11px;
  }
  .nav-item__link {
    color: #000;
  }
  .catalog-burger {
    display: none;
  }
}

.footer__row {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.footer__row_second {
  padding: 35px 0 27px 0;
}

.footer__wrapper {
  padding: 45px 0 0 0;
}

.footer__copyright {
  color: rgba(10, 37, 51, 0.6);
  font-size: 16px;
  line-height: 1.5;
}

.footer__logo {
  width: 400px;
  height: 110px;
}

.footer-privacy__link {
  color: rgba(10, 37, 51, 0.6);
  font-size: 16px;
  line-height: 1.5;
}

.footer__block_list {
  display: flex;
  margin-top: 40px;
}

.footer-info {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.footer-info__item {
  display: flex;
  align-items: flex-start;
}

.footer-info__item + .footer-info__item {
  margin-top: 7px;
}

.footer-info__img {
  width: 23px;
  height: 23px;
  margin-right: 12px;
  flex-shrink: 0;
}

.footer-info__img img {
  width: 100%;
  height: 100%;
