}

.footer-info__text {
  color: #151515;
  font-size: 16px;
  line-height: 1.53;
}

.footer-info__logo {
  max-width: 390px;
  width: 100%;
}

.footer-info__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer-list + .footer-list {
  margin-left: 104px;
}

.footer-list__title + .footer-list__item {
  margin-top: 40px;
}

.footer-list__item + .footer-list__item {
  margin-top: 13px;
}

.footer-list__text {
  color: #151515;
  font-weight: 500;
  font-size: 28px;
}

.footer-list__link {
  color: #151515;
  font-size: 16px;
  line-height: 1.53;
}

.footer-partners {
  display: flex;
  align-items: center;
}

@media screen and (min-width: 991px) {
  .footer__row {
    flex-direction: row;
  }
  .footer__row_second {
    align-items: flex-end;
  }
  .footer__block_list {
    margin-top: 0;
  }
  .footer-info {
    max-width: 340px;
  }
}

@media screen and (max-width: 991px) {
  .footer {
    border-top: 1px solid #EFEFEF;
    margin-top: 90px;
  }
  .footer__row_second {
    padding-top: 70px;
  }
  .footer__logo {
    width: 256px;
    height: 75px;
  }
  .footer-partners {
    order: 1;
    justify-content: space-between;
  }
  .footer__copyright {
    order: 2;
    margin: 65px 0 12px 0;
  }
  .footer-privacy {
