  }
}

@media screen and (max-width: 450px) {
  .footer {
    margin-top: 65px;
  }
  .footer__row_second {
    padding-top: 50px;
  }
  .footer__copyright {
    margin-top: 40px;
  }
  .footer__block_list {
    flex-direction: column;
  }
  .footer-list + .footer-list {
