    display: none;
  }
}

.section-hero {
  min-height: 365px;
  display: flex;
  align-items: center;
}

.section-hero__wrapper {
  width: 100%;
  max-width: 1045px;
  margin: 0 auto;
}

.section-hero__search {
  margin-top: 30px;
}

.hero-title__text {
  font-weight: 700;
  font-size: 40px;
  color: #fff;
  line-height: 1.1;
  text-shadow: 0px 1.11019px 0px rgba(0, 0, 0, 0.39);
