  width: 100%;
}

.hero-search {
  width: 100%;
}

.hero-search__submit {
  width: 215px;
  height: 45px;
  margin: 15px 0 0 auto;
  border-radius: 6px;
}

@media screen and (min-width: 991px) {
  .section-hero {
    min-height: 625px;
    background: url("../img/hero.jpg") center center no-repeat;
    background-size: cover;
  }
  .section-hero__search .search-block {
    box-shadow: 0px 4px 60px rgba(0, 0, 0, 0.65);
  }
  .section-hero__wrapper {
    padding-top: 130px;
  }
  .room-input:first-child .radio-input__checkmark {
    padding-left: 0;
    border-left: none;
  }
  .room-input:last-child .radio-input__checkmark {
