  }
  .hero-title__text {
    font-size: 32px;
  }
  .hero-search__submit {
    width: 100%;
    margin-top: 30px;
  }
}

.services-block__title {
  font-size: 26px;
  font-weight: 600;
}

.services-block__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: 70px;
  gap: 0;
  margin-top: 30px;
  min-width: 945px;
}

.services-item + .services-item {
  border-left: 1px solid #E1E1E1;
  padding-left: 25px;
}

.services-item {
  display: flex;
  align-items: flex-start;
}

.services-item__icon {
  width: 43px;
  height: 43px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background: #FF4D35;
}

.services-item__icon svg {
  max-height: 25px;
}

.services-item__icon + .services-item__content {
  margin-left: 20px;
}

.services-item__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.services-item__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
}

.services-item__text {
  font-size: 13px;
  line-height: 1.25;
  color: rgba(21, 21, 21, 0.8);
}

@media screen and (min-width: 991px) {
  .services-block__grid {
    gap: 50px;
    grid-auto-rows: 76px;
  }
  .services-item + .services-item {
    padding-left: 50px;
  }
  .services-item__icon + .services-item__content {
    margin-left: 25px;
  }
  .services-item__title {
    font-size: 18px;
  }
  .services-item__text {
    font-size: 14px;
  }
}

.object-block__title {
  font-weight: 600;
  font-size: 26px;
  color: #151515;
  text-align: center;
}

.object-block__grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-auto-rows: 170px;
  gap: 12px;
  margin-top: 25px;
}

.object-item {
  padding: 14px 12px;
  position: relative;
  border-radius: 13px;
  overflow: hidden;
}

.object-item__img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}

.object-item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.object-item__content {
  position: relative;
  z-index: 2;
}

.object-item__title, .object-item__text {
  color: #fff;
}

.object-item__title_black, .object-item__text_black {
  color: #000;
}

.object-item__title {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.21;
}

.object-item__text {
  font-size: 11px;
  line-height: 1.3;
  margin-top: 5px;
}

@media screen and (min-width: 500px) {
  .object-block__grid {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

@media screen and (min-width: 1024px) {
  .object-block__title {
    font-size: 32px;
    text-align: left;
  }
  .object-block__grid {
    grid-auto-rows: 320px;
    gap: 25px;
  }
  .object-item {
    padding: 25px;
  }
  .object-item__title {
    font-size: 24px;
  }
  .object-item__text {
    font-size: 16px;
  }
}

.items-slider__item {
