.search-block {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 7px;
}

.search-block__item {
  height: 66px;
  padding: 0 15px;
  display: flex;
  align-items: center;
}

.search-block__item_params {
  flex-shrink: 0;
}

.search-block__item + .search-block__item {
  border-left: 1px solid #E9E9E9;
}

.search-room {
  display: flex;
  align-items: center;
}

.search-input {
  display: flex;
  align-items: center;
}

.search-input__wrapper {
  display: flex;
  align-items: center;
  margin-left: 13px;
}

.search-input__wrapper > span {
  margin-left: 4px;
  font-size: 16px;
  display: block;
}

.search-input__wrapper input {
  -webkit-appearance: none;
  border: none;
  border-bottom: 1px solid #CECECE;
  width: 57px;
  height: 15px;
  outline: none;
  line-height: 14px;
}

.search-input__item + .search-input__item {
  margin-left: 10px;
}

.search-input__item span {
  color: #858C91;
  font-size: 16px;
  margin-right: 5px;
}

.search-select {
  max-width: 260px;
  width: 100%;
}

.search-select select {
  width: 100%;
  max-width: 260px;
}

.search-form__category {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-category__item + .search-category__item {
  margin-left: 8px;
}

.category-input {
  width: max-content;
}

.category-input__item:checked + .category-input__checkmark {
  background-color: #2C3144;
  color: #fff;
  border-color: transparent;
}

.category-input__checkmark {
  display: block;
  padding: 7px 18px;
  border: 1px solid #F0F1F1;
  border-radius: 36.9182px;
  font-size: 15px;
  font-weight: 500;
  transition: .3s background-color, .3s color;
}

.category-input__checkmark:hover {
  cursor: pointer;
}

.room-input__item:checked + .room-input__checkmark {
  color: #3D7DDA;
}

.room-input__checkmark {
  padding: 0 20px;
  border-left: 1px solid #DDDEE0;
  font-size: 16px;
  transition: .3s color, .3s background-color;
}

.room-input__checkmark:hover {
  cursor: pointer;
}

@media screen and (min-width: 991px) {
  .category-input__item:checked + .category-input__checkmark {
    background-color: #fff;
    color: #151515;
  }
  .category-input__checkmark {
    background: rgba(11, 14, 22, 0.66);
    border: none;
    color: #Fff;
  }
  .search-input__item-sign {
    display: none;
  }
}

@media screen and (max-width: 990px) {
  .logo__text {
    display: none;
  }
  .search-block {
    flex-direction: column;
    align-items: flex-start;
  }
  .search-block__item {
    height: auto;
  }
  .search-block__item_room {
    width: 100%;
  }
  .search-block__item + .search-block__item {
    border-left: none;
    margin-top: 10px;
  }
  .search-room {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    border: 1px solid #E4E4E4;
    border-radius: 8px;
  }
  .search-input__title {
    width: 85px;
  }
  .search-input__wrapper {
    height: 48px;
    border: 1px solid #E4E4E4;
    border-radius: 8px;
  }
  .search-input__wrapper > span {
    display: none;
  }
  .search-input__wrapper input {
    border: none;
  }
  .search-input__wrapper span {
    margin: 0;
  }
  .search-input__item {
    padding: 0 12px;
    height: 100%;
    display: flex;
    align-items: center;
  }
  .search-input__item-sign {
    width: 11px;
    display: block;
  }
  .search-input__item + .search-input__item {
    margin-left: 0;
    border-left: 1px solid #E4E4E4;
  }
  .room-input {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    border-left: 1px solid #E4E4E4;
  }
  .room-input:first-child {
    border-left: none;
  }
  .room-input:first-child .room-input__checkmark {
    border-bottom-left-radius: 8px;
    border-top-left-radius: 8px;
  }
  .room-input:last-child .room-input__checkmark {
    border-bottom-right-radius: 8px;
    border-top-right-radius: 8px;
  }
  .room-input__item:checked + .room-input__checkmark {
    background-color: #EFF4FA;
    border: 1px solid #3D7DDA;
  }
  .room-input__checkmark {
    border-left: none;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .room-input__label {
    width: 100%;
    height: 100%;
  }
  .header-row_first {
    height: 54px;
  }
  .header-row_first .header__address {
    display: none;
  }
  .header-row_first .header__phone {
    margin-bottom: 0;
  }
  .header__logo {
    height: 40px;
  }
}

@media screen and (max-width: 450px) {
  .search-block__item {
    padding: 0;
    width: 100%;
  }
  .search-input {
    width: 100%;
    justify-content: space-between;
  }
}
